@import url('https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,500;1,800&family=Montserrat:wght@200;300;600;700&family=Noto+Sans+TC:wght@100;400&family=Nunito+Sans:ital,wght@0,200;0,300;0,600;1,600&family=Raleway:wght@100;300;800&display=swap');


.App{
  font-family: 'Nunito Sans', sans-serif;
  margin: 0;
  background: rgb(247, 247, 247);
  padding: 0;
  height: 100%;
  font-weight: 500;
}
.App > .container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.heading_app{
  padding-top: 30px;
  width: 100%;
  align-items: center;
  display: flex;
  padding-left: 20px;
  cursor: pointer;
  background: white;
  border-bottom: 1px solid lightgray;
  padding-bottom: 30px;
}
.heading_app_left{
  flex: 0.95;
}
.heading_app_left img{
  max-width: 320px;
}
.heading_app_left > h1:hover{
  color: rgb(13, 182, 117);
}
.heading_app_right > span:hover{
  color: rgb(13, 117, 77);
}
.covid{
  margin-top: 60px;
}
h1.covid{
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 28px;
}
p.summary{
  font-family: 'Nunito Sans', sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 28px;
}
.covid_summary button{
  margin-bottom: 60px;
}
.covid img{
  height: 40px;
}
.footer_app{
  width: 100%;
  position: absolute;
  top: auto;
  background: white;
  border-top: 1px solid lightgray;
  padding-top: 18px;
}
p.footer__heading{
  text-align: center;
  font-family: 'Nunito Sans', sans-serif;
  font-size: 14px;
  color: rgb(80, 80, 80);
}
p.footer__heading > a{
  font-weight: 600;
  color: rgb(75, 75, 75);
}
@media screen and (max-width: 720px){
  .App > .container{
    display: block;
  }
  
  .covid_summary button{
    margin-bottom: 160px;
  }
}
@media screen and (max-width: 1220px){
  .App > .container{
    display: block;
  }
  
  .covid_summary button{
    margin-bottom: 160px;
  }
}
@media screen and (max-width: 320px){
  
  .covid_summary button{
    margin-bottom: 60px;
  }
}

