.chart{
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 40px;
    cursor: pointer;
    margin-bottom: 50px;
}
.heading_chart{
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 24px;
    padding-top: 20px;
    margin-bottom: 22px;
    cursor: text;
    padding-bottom: 20px;
    border-bottom: 1px solid lightgray;
}