@import url('https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:ital,wght@100;200;300;400;500;600;700&family=Montserrat:wght@200;300;400;500;600;700&family=Noto+Sans+TC:wght@100;400&family=Nunito+Sans:ital,wght@0,200;0,300;0,600;1,600&family=Raleway:wght@100;300;800&display=swap');

.card{
    background: transparent;
    box-shadow: none;
    display: flex;
    margin-top: 50px;
    flex-direction: row;
    margin-bottom: 50px;
    width: 100%;
}
.infected_card{
    background: white;
    min-width: 350px;
    border-left: 4px solid #21e75d;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: none;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    border-right: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-right: 10px;
}
.infected_card:hover{
    cursor: pointer;
    border-left: 4px solid #21e75d;
    -webkit-box-shadow: 0px 0px 12px -3px rgba(0, 0, 0, 0.411);
    -moz-box-shadow:    0px 0px 12px -3px rgba(0, 0, 0, 0.452);
    box-shadow:         0px 0px 12px -3px rgba(0, 0, 0, 0.384);
}

.recovered_card{
    background: white;
    border-bottom-left-radius: 30px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    min-width: 350px;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: none;
    border-left: 1px solid lightgray;
    border-right: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-right: 10px;
}
.recovered_card:hover{
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 12px -3px rgba(0, 0, 0, 0.411);
    -moz-box-shadow:    0px 0px 12px -3px rgba(0, 0, 0, 0.452);
    box-shadow:         0px 0px 12px -3px rgba(0, 0, 0, 0.384);
}

.death_card{
    background: white;
    min-width: 350px;
    border-right: 4px solid #e43030;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: none;
    border-bottom-left-radius: 30px;
    border-top-left-radius: 30px;
    border-left: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-right: 10px;
}
.death_card:hover{
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 12px -3px rgba(0, 0, 0, 0.411);
    -moz-box-shadow:    0px 0px 12px -3px rgba(0, 0, 0, 0.452);
    box-shadow:         0px 0px 12px -3px rgba(0, 0, 0, 0.384);
}
.card .content .value{
    font-family: 'Montserrat', sans-serif;
    font-size: 13px;
    font-weight: 600;
    color: rgb(85, 85, 85);
}
h4.content{
    font-family: 'Alegreya Sans SC', sans-serif;
    font-size: 16px;
    letter-spacing: 1px;
    color:  rgb(53, 53, 53);
}
span.active_cases{
    font-family: 'Alegreya Sans Sc', sans-serif;
    font-weight: 400;
    font-size: 15px;
    letter-spacing: 1px;
    color: darkslategrey;
}
.card-content img {
    width: 20%;
    margin-bottom: 15px;
}
@media screen and (max-width: 1224px){
    .card{
        max-width: 420px;
        display: flex;
        flex-direction: column;
        margin-top: 20px;
    }
  }
  
  