.App{
  font-family: 'Nunito Sans', sans-serif;
  margin: 0;
  background: rgb(247, 247, 247);
  padding: 0;
  height: 100%;
  font-weight: 500;
}
.App > .container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.heading_app{
  padding-top: 30px;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  padding-left: 20px;
  cursor: pointer;
  background: white;
  border-bottom: 1px solid lightgray;
  padding-bottom: 30px;
}
.heading_app_left{
  -webkit-flex: 0.95 1;
          flex: 0.95 1;
}
.heading_app_left img{
  max-width: 320px;
}
.heading_app_left > h1:hover{
  color: rgb(13, 182, 117);
}
.heading_app_right > span:hover{
  color: rgb(13, 117, 77);
}
.covid{
  margin-top: 60px;
}
h1.covid{
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 28px;
}
p.summary{
  font-family: 'Nunito Sans', sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 28px;
}
.covid_summary button{
  margin-bottom: 60px;
}
.covid img{
  height: 40px;
}
.footer_app{
  width: 100%;
  position: absolute;
  top: auto;
  background: white;
  border-top: 1px solid lightgray;
  padding-top: 18px;
}
p.footer__heading{
  text-align: center;
  font-family: 'Nunito Sans', sans-serif;
  font-size: 14px;
  color: rgb(80, 80, 80);
}
p.footer__heading > a{
  font-weight: 600;
  color: rgb(75, 75, 75);
}
@media screen and (max-width: 720px){
  .App > .container{
    display: block;
  }
  
  .covid_summary button{
    margin-bottom: 160px;
  }
}
@media screen and (max-width: 1220px){
  .App > .container{
    display: block;
  }
  
  .covid_summary button{
    margin-bottom: 160px;
  }
}
@media screen and (max-width: 320px){
  
  .covid_summary button{
    margin-bottom: 60px;
  }
}


.card{
    background: transparent;
    box-shadow: none;
    display: -webkit-flex;
    display: flex;
    margin-top: 50px;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-bottom: 50px;
    width: 100%;
}
.infected_card{
    background: white;
    min-width: 350px;
    border-left: 4px solid #21e75d;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: none;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    border-right: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-right: 10px;
}
.infected_card:hover{
    cursor: pointer;
    border-left: 4px solid #21e75d;
    box-shadow:         0px 0px 12px -3px rgba(0, 0, 0, 0.384);
}

.recovered_card{
    background: white;
    border-bottom-left-radius: 30px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    min-width: 350px;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: none;
    border-left: 1px solid lightgray;
    border-right: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-right: 10px;
}
.recovered_card:hover{
    cursor: pointer;
    box-shadow:         0px 0px 12px -3px rgba(0, 0, 0, 0.384);
}

.death_card{
    background: white;
    min-width: 350px;
    border-right: 4px solid #e43030;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: none;
    border-bottom-left-radius: 30px;
    border-top-left-radius: 30px;
    border-left: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-right: 10px;
}
.death_card:hover{
    cursor: pointer;
    box-shadow:         0px 0px 12px -3px rgba(0, 0, 0, 0.384);
}
.card .content .value{
    font-family: 'Montserrat', sans-serif;
    font-size: 13px;
    font-weight: 600;
    color: rgb(85, 85, 85);
}
h4.content{
    font-family: 'Alegreya Sans SC', sans-serif;
    font-size: 16px;
    letter-spacing: 1px;
    color:  rgb(53, 53, 53);
}
span.active_cases{
    font-family: 'Alegreya Sans Sc', sans-serif;
    font-weight: 400;
    font-size: 15px;
    letter-spacing: 1px;
    color: darkslategrey;
}
.card-content img {
    width: 20%;
    margin-bottom: 15px;
}
@media screen and (max-width: 1224px){
    .card{
        max-width: 420px;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-top: 20px;
    }
  }
  
  
.chart{
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    margin-top: 40px;
    cursor: pointer;
    margin-bottom: 50px;
}
.heading_chart{
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 24px;
    padding-top: 20px;
    margin-bottom: 22px;
    cursor: text;
    padding-bottom: 20px;
    border-bottom: 1px solid lightgray;
}
